.buttonWrapper {
    display: flex;
    justify-content: flex-end;
    max-width: 1280px;
    margin: 0 auto 16px;
}

.createButton {
    background: #3498db;
    color: white;
    border: none;
    border-radius: 6px;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.2s ease, box-shadow 0.3s ease;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
}

.createButton:disabled {
    background: #bdc3c7;
    color: #7f8c8d;
    cursor: not-allowed;
    box-shadow: none;
    transform: none;
}

.createButton:hover:enabled {
    background: #2980b9;
    box-shadow: 0 5px 12px rgba(0, 0, 0, 0.15);
    transform: translateY(-2px);
}

.createButton:active:enabled {
    background: #1f5a7e;
    transform: translateY(0);
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
}

@media (max-width: 700px) {
    .buttonWrapper {
        max-width: 100%;
        justify-content: flex-end;
    }

    .createButton {
        font-size: 12px;
        padding: 6px 12px;
    }
}
