.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: #f8f9fa;
  }
  
  .contentWrapper {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
  }
  
  .messageBox {
    background: #fff;
    border-radius: 15px;
    padding: 40px;
    width: 100%;
    max-width: 600px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .mainMessage {
    font-size: 28px;
    font-weight: 600;
    color: #e74c3c;
    margin-bottom: 20px;
  }
  
  .subMessage {
    font-size: 18px;
    color: #555;
    margin-bottom: 20px;
  }
  
  .details {
    font-size: 16px;
    color: #777;
    margin-bottom: 30px;
  }

  @media (max-width: 600px) {
    .mainMessage {
      font-size: 24px;
    }
  
    .subMessage {
      font-size: 16px;
    }
  
    .details {
      font-size: 14px;
    }
  }
  