.commentForm {
    display: flex;
    flex-direction: column;
    gap: 12px;
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    max-width: 800px;
    margin: 20px auto;
}


.closedMessage {
    background-color: #f8d7da; 
    color: #721c24; 
    border: 1px solid #f5c6cb; 
    padding: 15px;
    border-radius: 6px;
    font-size: 16px;
    margin-bottom: 10px;
    text-align: center;
}
.notLoggedInMessage {
    background-color: #f8d7da; 
    color: #721c24;
    border: 1px solid #f5c6cb; 
    padding: 15px;
    border-radius: 6px;
    font-size: 16px;
    text-align: center;
    margin-top: 20px;
}

.commentInput {
    width: 100%;
    min-height: 100px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 16px;
    resize: vertical;
}

.commentActions {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.submitButton {
    background: linear-gradient(135deg, #007bff, #0056b3);
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 6px;
    font-size: 16px;
    cursor: pointer;
    transition: background 0.3s ease;
}

.submitButton:disabled {
    background: #ccc;
    cursor: not-allowed;
}

.submitButton:hover:not(:disabled) {
    background: linear-gradient(135deg, #0056b3, #003d82);
}

.switch {
    position: relative;
    width: 70px;
    height: 36px;
    background-color: #00bcd4; 
    border-radius: 40px; 
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: -5px;
    transition: background-color 0.4s ease, box-shadow 0.4s ease;
    border-radius: 30px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 22px;
    width: 22px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s ease;
    border-radius: 50%;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1); 
}

input:checked + .slider:before {
    transform: translateX(30px);
}

.labelText {
    font-size: 14px;
    color: #555;
    margin-left: 10px;
}

@media (max-width: 1200px) {
    .commentForm {
        max-width: 700px;
        padding: 18px;
    }
}

@media (max-width: 800px) {
    .commentForm {
        max-width: 90%;
        padding: 15px;
    }

    .commentActions {
        flex-direction: column;
        align-items: center;
    }

    .commentBlock{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 5px;
    }

    .submitButton {
        width: 100%;
    }
}

@media (max-width: 600px) {
    .commentForm {
        max-width: 100%;
        padding: 12px;
    }

    .commentInput {
        min-height: 80px;
        font-size: 14px;
    }

    .switch {
        width: 60px;
        height: 30px;
    }

    .slider:before {
        height: 18px;
        width: 18px;
    }

    input:checked + .slider:before {
        transform: translateX(26px);
    }

    .labelText {
        font-size: 12px;
    }
}

@media (max-width: 400px) {
    .commentForm {
        padding: 10px;
    }

    .commentInput {
        font-size: 13px;
    }

    .submitButton {
        font-size: 14px;
        padding: 8px 16px;
    }

    .labelText {
        font-size: 12px;
    }
}