@import url('https://fonts.googleapis.com/css2?family=Arial&display=swap');

.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-top: 30px;
    font-family: 'Arial', sans-serif;
}

button {
    background-color: #3498db;
    color: white;
    border: none;
    border-radius: 10px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease, transform 0.2s ease, box-shadow 0.3s ease;
    box-shadow: 0 4px 10px rgba(52, 152, 219, 0.3);
}

button:disabled {
    background-color: #bdc3c7;
    cursor: not-allowed;
    box-shadow: none;
}

button:hover:not(:disabled) {
    background-color: #2980b9;
    transform: translateY(-2px);
    box-shadow: 0 6px 15px rgba(52, 152, 219, 0.4);
}

button:active {
    transform: translateY(0);
    box-shadow: 0 4px 10px rgba(52, 152, 219, 0.2);
}

span {
    font-size: 18px;
    font-weight: 600;
    color: #333;
    display: flex;
    align-items: center;
    gap: 10px;
}

.pageInput {
    width: 70px;
    text-align: center;
    font-size: 18px;
    padding: 8px;
    border-radius: 10px;
    border: 1px solid #3498db;
    outline: none;
    background-color: #f8f9fa;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
    transition: background-color 0.3s ease;
}

.pageInput:focus {
    border-color: #2980b9;
    box-shadow: 0 0 10px rgba(41, 128, 185, 0.5);
    background-color: #ffffff;
    cursor: text;
}

.pageInput:disabled {
    background-color: #ecf0f1;
    cursor: not-allowed;
}

.pageInput::-webkit-outer-spin-button,
.pageInput::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.pageInput::selection {
    background-color: transparent; /* Убираем выделение текста */
}

.pagination button,
.pagination span {
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.pagination button:hover {
    opacity: 0.8;
}

.pagination span {
    opacity: 1;
    transition: opacity 0.3s ease;
}

/* Адаптивные стили */
@media (max-width: 1000px) {
    .pagination {
        gap: 15px;
    }

    button {
        font-size: 14px;
        padding: 8px 15px;
    }

    span {
        font-size: 16px;
    }

    .pageInput {
        width: 60px;
        font-size: 16px;
    }
}

@media (max-width: 800px) {
    .pagination {
        gap: 12px;
    }

    button {
        font-size: 13px;
        padding: 7px 12px;
    }

    span {
        font-size: 14px;
    }

    .pageInput {
        width: 55px;
        font-size: 14px;
    }
}

@media (max-width: 500px) {
    .pagination {
        gap: 10px;
    }

    button {
        font-size: 12px;
        padding: 6px 10px;
    }

    span {
        font-size: 14px;
    }

    .pageInput {
        width: 50px;
        font-size: 14px;
    }
}

@media (max-width: 410px) {
    .pagination {
        gap: 8px;
    }

    button {
        font-size: 10px;
        padding: 5px 8px;
    }

    span {
        font-size: 12px;
    }

    .pageInput {
        width: 30px;
        font-size: 10px;
        margin-top: 5px;
    }
}
