.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modalContent {
    background-color: #fff;
    padding: 30px;
    border-radius: 12px;
    width: 400px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
    text-align: center;
    animation: fadeIn 0.3s ease-out;
}

.input, .select {
    width: 100%;
    padding: 12px 14px;
    margin: 20px 0;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 16px;
    outline: none;
    transition: border-color 0.3s;
}

.input:focus, .select:focus {
    border-color: #3498db;
    box-shadow: 0 0 4px rgba(52, 152, 219, 0.5);
}

.select {
    background-color: #f9f9f9;
    appearance: none;
    cursor: pointer;
}

.select:hover {
    background-color: #f1f1f1;
}

.modalActions {
    display: flex;
    justify-content: space-between;
    gap: 10px;
    margin-top: 20px;
}

.cancelButton, .saveButton {
    padding: 12px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 8px;
    transition: background-color 0.3s, transform 0.2s;
}

.cancelButton {
    background-color: #ccc;
    color: #333;
}

.saveButton {
    background-color: #3498db;
    color: #fff;
}

.cancelButton:hover {
    background-color: #bbb;
    transform: scale(1.05);
}

.saveButton:hover {
    background-color: #2980b9;
    transform: scale(1.05);
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10%);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
