.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1000px;
    padding: 0;
    border: none;
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    background-color: rgba(255, 255, 255, 0.95);
    z-index: 1000;
    animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translate(-50%, -60%);
    }
    to {
        opacity: 1;
        transform: translate(-50%, -50%);
    }
}

.modalContent {
    padding: 30px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.modalTitle {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
}

.input {
    width: 100%;
    padding: 12px 15px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    transition: border-color 0.3s ease;
}

.input:focus {
    border-color: #3498db;
    outline: none;
    background-color: white;
}

.buttons {
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

.moveButton {
    flex: 1;
    padding: 12px;
    background-color: #3498db;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    font-size: 16px;
    font-weight: bold;
}

.moveButton:hover {
    background-color: #2980b9;
}

.cancelButton {
    flex: 1;
    padding: 12px;
    background-color: #e74c3c;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    font-size: 16px;
    font-weight: bold;
}

.cancelButton:hover {
    background-color: #c0392b;
}

@media (max-width: 1000px) {
    .modal {
        width: 700px;
    }
}

@media (max-width: 700px) {
    .modal {
        width: 400px;
    }
}

@media (max-width: 400px) {
    .modal {
        width: 100%;
        max-width: 100%;
    }

    .modalContent {
        padding: 15px;
    }

    .input {
        font-size: 14px;
    }

    .moveButton,
    .cancelButton {
        font-size: 14px;
    }
}
