.dropdown {
    position: relative;
    display: inline-block;
  }
  .toggleButton {
    background: none;
    border: none;
    cursor: pointer;
    padding: 8px;
    border-radius: 50%;
    transition: background-color 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .toggleButton:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }
  
  .toggleButton:focus {
    outline: none;
  }
  
  .icon {
    width: 24px;
    height: 20px;
    position: relative;
  }
  
  .icon div {
    position: absolute;
    background-color: #333;
    height: 3px;
    width: 100%;
    border-radius: 3px;
    transition: all 0.3s ease;
  }
  
  .icon div:nth-child(1) {
    top: 0;
  }
  
  .icon div:nth-child(2) {
    top: 50%;
    transform: translateY(-50%);
  }
  
  .icon div:nth-child(3) {
    bottom: 0;
  }
  
  .icon.open div:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
  }
  
  .icon.open div:nth-child(2) {
    opacity: 0;
  }
  
  .icon.open div:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
  }
  
  .menu {
    position: absolute;
    top: 40px;
    right: 0;
    background: white;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
    z-index: 10;
  }
  
  .menuItem {
    padding: 10px 20px;
    cursor: pointer;
    background: white;
    color: #333;
    transition: background-color 0.2s ease, color 0.2s ease;
  }
  
  .menuItem:hover {
    background-color: #3498db;
    color: white;
  }
  
  .menuItem:active {
    background-color: #2980b9;
  }
  