.userList {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
}
  
  .userCard {
    background: linear-gradient(135deg, #8e44ad, #3498db);
    color: white;
    padding: 15px;
    border-radius: 10px;
    width: 100%;
    text-align: center;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease-in-out;
    margin-bottom: 20px;
  }
  
  .userCard:hover {
    transform: scale(1.05);
  }
  
  .userLink {
    color: white;
    text-decoration: none;
    font-weight: bold;
    transition: 0.3s;
  }
  
  .userLink:hover {
    text-decoration: underline;
  }
  
.notFound{
        text-align: center;
        font-size: 18px;
        color: #666;
        margin-top: 20px;
}

  @media (max-width: 720px) {
    .userList {
      width: 70%;
    }
  
    .userCard {
      padding: 12px;
      font-size: 16px;
    }
  }
  
  @media (max-width: 550px) {
    .userCard {
      padding: 10px;
      font-size: 14px;
    }
  }
  