.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.name {
  font-size: 20px;
  margin-right: 10px;
  margin-left: 40px;
  color: inherit;
  transition: .5s;
  display: flex;
  align-items: center;
}

.name:hover,
.name:focus,
.link:hover,
.link:focus {
  opacity: 0.5;
  transform: scale(1.3);
}

.link {
  border: none;
  width: fit-content;
  cursor: pointer;
  font-size: 20px;
  color: inherit;
  transition: .5s;
  margin-left: 30px;
  margin-right: 30px;
  display: flex;
  align-items: center;
}

.name::before,
.link::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.3s ease-in-out;
}

.name:hover::before,
.name:focus::before,
.link:hover::before,
.link:focus::before {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.name::after {
  margin-left: 10px;
  content: '';
  background-image: url('./images/user.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 50px;
  width: 50px;
}

.link::after {
  margin-left: 10px;
  content: '';
  background-image: url('./images/exit.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 50px;
  width: 50px;
}

/* media */

@media (max-width: 1000px) {

  .link,
  .name {
    font-size: 20px;
  }

  .link::after,
  .name::after {
    height: 30px;
    width: 30px;
  }
}

@media (max-width: 700px) {

  .link,
  .name {
    font-size: 15px;
  }

  .link::after,
  .name::after {
    height: 25px;
    width: 25px;
  }
}

@media (max-width: 400px) {
  .name {
    margin: 0;
    margin-bottom: 10px;
  }

  .link{
    margin: 0;
  }

  .name:hover,
  .name:focus,
  .link:hover,
  .link:focus {
    opacity: 0.5;
    transform: scale(1.1);
  }
  
  .container{
    display: block;
  }
}