@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

* {
    font-family: 'Inter', sans-serif;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.loader {
    width: 50px;
    height: 50px;
    border: 5px solid rgba(0, 0, 0, 0.1);
    border-left-color: #007bff;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin: 50px auto;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.topicHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(135deg, #ffffff, #f9f9f9);
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    position: relative;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.topicHeader:hover {
    transform: translateY(-5px);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}

.topicInfo {
    flex: 1;
    max-width: 75%;
}

.topicTitle {
    font-size: 28px;
    font-weight: 700;
    color: #333;
    margin-bottom: 12px;
    position: relative;
}

.topicTitle::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 50px;
    height: 3px;
    background: linear-gradient(90deg, #007bff, #00bfff);
    border-radius: 2px;
}

.topicDescription {
    font-size: 18px;
    color: #555;
    margin-bottom: 12px;
    line-height: 1.6;
    word-wrap: break-word; 
    overflow-wrap: break-word; 
    white-space: pre-wrap; 
}

.topicDate {
    font-size: 14px;
    color: #888;
    margin-bottom: 12px;
}

.topicAuthor {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    min-width: 150px;
    padding: 10px;
    border-left: 2px solid #ddd;
}

.authorLabel {
    font-size: 14px;
    color: #666;
    margin-bottom: 6px;
}

.authorName {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: #007bff;
    font-weight: 600;
    text-decoration: none;
    transition: color 0.3s, transform 0.3s;
    gap: 8px;
}

.authorName:hover {
    color: #0056b3;
    transform: translateX(5px);
}

.userIcon {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background: #f0f0f0;
    padding: 4px;
}

@media (max-width: 900px) {
    .topicHeader {
        flex-direction: column;
        align-items: flex-start;
        padding: 20px;
    }

    .topicAuthor {
        border-left: none;
        border-top: 2px solid #ddd;
        width: 100%;
        padding-top: 10px;
        margin-top: 15px;
    }
}

@media (max-width: 600px) {
    .topicTitle {
        font-size: 24px;
    }

    .topicDescription {
        font-size: 16px;
    }

    .topicAuthor {
        flex-direction: column;
        justify-content: center;
    }
}
