.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalContent {
    background-color: #fff;
    padding: 30px;
    border-radius: 8px;
    width: 400px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    text-align: center;
}

.input {
    width: 100%;
    padding: 10px;
    margin: 20px 0;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
}

.modalActions {
    display: flex;
    justify-content: space-between;
}

.cancelButton, .saveButton {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s;
}

.cancelButton {
    background-color: #ccc;
}

.saveButton {
    background-color: #3498db;
    color: #fff;
}

.cancelButton:hover {
    background-color: #bbb;
}

.saveButton:hover {
    background-color: #2980b9;
}
