.header{ 
    background: linear-gradient(to right, #8e44ad, #3498db);
    width:100%;
    color:white;
    font-size: 30px;
    letter-spacing: 0.03em;
    font-family: "Inter", sans-serif;
    padding:10px;
    box-shadow: 0px 8px 15px rgba(52, 152, 219, 0.5), 
                0px 12px 20px rgba(142, 68, 173, 0.4), 
                0px 3px 5px rgba(0, 0, 0, 0.3);
                margin-bottom: 10px;
}

.container{
    max-width:1280px;
    margin: 0 auto;
    display:flex;
    justify-content: space-between;
}

.logo {
    margin-left:10px;
    background-image: url(./images/logo_transparent.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100px;
    width: 100px;
}

.loading{
    align-self: center;
    margin-right: 10px;
    margin-left: 40px;
}

.loading::after {
    content: '';
    animation: dots 2s steps(4, end) infinite;
}

@keyframes dots {
    0% {
        content: ' ';
    }
    25% {
        content: '.';
    }
    50% {
        content: '..';
    }
    75% {
        content: '...';
    }
    100% {
        content: ' ';
    }
}

.searchForm {
    display: flex;
    align-items: center;
    gap: 10px;
}

.searchInput {
    width: 250px;
    padding: 10px;
    border-radius: 20px;
    border: 1px solid #ddd;
    font-size: 16px;
    outline: none;
    text-align: center;
    transition: 0.3s;
}

.searchInput::placeholder {
    color: white;
    opacity: 1;
}

.searchInput:focus {
    border-color: #3498db;
    box-shadow: 0px 0px 5px rgba(52, 152, 219, 0.5);
}

.searchButton {
    background: #3498db;
    border: none;
    padding: 10px 15px;
    border-radius: 50%;
    cursor: pointer;
    color: white;
    font-size: 18px;
    transition: 0.3s;
}

.searchButton:hover {
    background: #2980b9;
}

/* Media */

@media (max-width: 550px) {
    .logo{
    width:80px;
    height: 80px;
    padding: 0;
    margin: 0;
    }
    .loading{
        font-size: 20;
    }
}

@media (max-width: 980px) {
    .header{
        padding: 0;
        padding-bottom:5px;
    }
    .container{
        display:flex;
        flex-direction: column;
        align-items: center;
    }
    .logo{
        height: 80px;
        width: 80px;
    }
    .loading{
        font-size: 14px;
    }

    .searchInput {
        width: 80%;
        font-size: 14px;
        padding: 8px;
        text-align: center;
    }

}

