.container{
    display: flex;
    align-items: center;
    margin-right: 10px;
    margin-left: 40px;
    gap:40px;
}

.link{
    color:inherit;
    transition: .5s;
    display:flex;
    align-items: center;
}

.link:hover, .link:focus{
    opacity: .5;
}

.registartion, .login{
    position: relative;;
}

.registartion::before,
.login::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.3s ease-in-out;
}

.registartion:hover::before, .registartion:focus::before, .registartion:active::before,
.login:hover::before, .login:focus::before, .login:active::before {
  transform: scaleX(1); 
  transform-origin: bottom left; 
}

.registartion::after{
    margin-left:10px;
    content:'';
    background-image: url('./icons/registration.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 40px;
    width: 40px;
}

.login::after{
    margin-left:10px;
    content:'';
    background-image: url('./icons/login.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 40px;
    width: 40px;
}

/* Media */

@media (max-width: 1000px) {
    .link {
      font-size: 20px;
    }
    .login::after, .registartion::after{
        height: 30px;
        width: 30px;
    }
}

@media (max-width: 550px) {
    .link {
      font-size: 14px;
    }
    .login::after, .registartion::after{
        height: 20px;
        width: 20px;
    }
    .container{
        margin-left: 0;
    }
}