
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f7f9fc;
  color: #333;
}

.wrapper {
  max-width: 800px;
  margin: 40px auto;
  background-color: #fff;
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.title {
  font-size: 26px;
  font-weight: 700;
  margin-bottom: 25px;
  text-align: center;
  color: #333;
}

.formWrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

textarea {
  width: 100%;
  padding: 15px;
  border-radius: 10px;
  border: 1px solid #ccc;
  font-size: 16px;
  background-color: #f5f5f5;
  transition: all 0.3s ease;
  resize: none;
}

textarea:focus {
  border-color: #3498db;
  background-color: #fff;
  outline: none;
}

.textTitle {
  height: 60px;
}

.textSubtitle {
  height: 120px;
}

.submitButton {
  padding: 14px 28px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease, transform 0.2s ease;
  align-self: flex-end;
}

.submitButton:hover {
  background-color: #2980b9;
  transform: translateY(-2px);
}

.submitButton:active {
  background-color: #1f5a7e;
  transform: translateY(0);
}

.checkboxWrapperOut {
  display: flex;
  gap: 20px;
}

.checkboxWrapper {
  display: flex;
  align-items: center;
  gap: 15px;
}

.checkboxWrapper input {
  appearance: none;
  width: 30px;
  height: 30px;
  border: 2px solid #ccc;
  border-radius: 10px;
  background-color: #fff;
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease;
}

.checkboxWrapper input:checked {
  background-color: #3498db;
  border-color: #3498db;
}

.checkboxWrapper input:checked::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.6);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: white;
  box-shadow: 0 0 0 3px #fff;
  transition: all 0.3s ease;
}

.checkboxWrapper input:focus {
  outline: none;
  border-color: #2980b9;
}

.checkboxWrapper input:hover {
  border-color: #2980b9;
}

.checkboxWrapper label {
  font-size: 18px;
  color: #333;
  cursor: pointer;
  font-weight: 500;
  transition: color 0.3s ease;
}

.checkboxWrapper input:checked + label {
  color: #3498db;
}

.checkboxWrapper input:checked:hover + label {
  color: #2980b9;
}

/* media */

@media (max-width: 700px) {
  .wrapper {
    padding: 20px;
  }

  .title {
    font-size: 22px;
  }

  .submitButton {
    padding: 12px 24px;
    font-size: 14px;
  }

  .formWrapper {
    gap: 15px;
  }

  textarea {
    padding: 12px;
    font-size: 14px;
  }

  .checkboxWrapper {
    gap: 10px;
  }

  .checkboxWrapper label {
    font-size: 16px;
  }
}

@media (max-width: 400px) {
  .wrapper {
    padding: 15px;
  }

  .title {
    font-size: 20px;
  }

  .submitButton {
    padding: 10px 20px;
    font-size: 14px;
  }

  .formWrapper {
    gap: 10px;
  }

  textarea {
    padding: 10px;
    font-size: 13px;
  }

  .checkboxWrapper {
    gap: 8px;
  }

  .checkboxWrapper label {
    font-size: 14px;
  }
}
